#hero-area {
  padding-top: 180px;
  background-color: #179C7F;
  color: white;
}
#hero-area h1 {
  color: white;
  text-transform: uppercase;
  font-size: 32px;
  position: relative;
  margin-bottom: 24px;
}
#hero-area h1::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -12px;
  background: #fff;
  height: 3px;
  width: 110px;
}
#hero-area p {
  font-size: 18px;
}
#apply {
  padding: 70px 0px;
  background-color: #179C7F;
  color: white;
  text-align: center;
}
#apply .heading {
  padding: 0;
}
#apply .heading h2 {
  text-transform: uppercase;
  color: white;
  font-size: 25px;
  margin-bottom: 10px;
  font-weight: 400;
  line-height: 1.5;
}
#apply .ant-btn.ant-btn-background-ghost:hover {
  background-color: white !important;
  border-color: white;
  color: #117d6b;
}
#apply .ant-btn:nth-of-type(1) {
  margin-right: 1em;
}
#materials {
  padding: 70px 0px;
  background-color: #179C7F;
  color: white;
  text-align: center;
}
#materials .heading {
  padding: 0;
}
#materials .heading h2 {
  text-transform: uppercase;
  color: white;
  font-size: 25px;
  margin-bottom: 10px;
  font-weight: 400;
  line-height: 1.5;
}
#materials .ant-btn.ant-btn-background-ghost:hover {
  background-color: white !important;
  border-color: white;
  color: #117d6b;
}
#materials .ant-btn:nth-of-type(1) {
  margin-right: 1em;
}
#contact {
  text-align: center;
}
#photo.main-container {
  padding-bottom: 0;
}
#photo .ant-carousel .slick-slide {
  text-align: center;
  height: 529px;
  overflow: hidden;
}
#program.main-container {
  padding-bottom: 0;
}
#venue {
  text-align: center;
}
#venue.main-container {
  padding-bottom: 0;
}
#venue img {
  padding-bottom: 1em;
}
.main-container {
  padding: 0 64px 144px 64px;
}
.heading {
  text-align: center;
  padding-top: 50px;
  padding-bottom: 20px;
}
.heading h2 {
  padding-bottom: 30px;
  text-transform: capitalize;
  font-weight: 300;
  font-size: 40px;
  line-height: 40px;
}
.sub-heading {
  margin: 20px 0 30px;
}
h2 {
  font-size: 30px;
  line-height: 35px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
}
